export default {
	props:{
		item:Object,
		index:Number
	},
	methods:{
		clickevent(){
			switch (this.item.clicktype){
				case "navigateTo":
				if(this.item.url){ uni.navigateTo({ url:this.item.url}); }
					break;
				case "switchTab":
				if(this.item.url){ uni.switchTab({url:this.item.url}) }
					break;
			}
		}
	}
}
